@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:opsz,wght@6..12,600;6..12,700&display=swap');
ul {
	list-style: none;
	padding-left: 0;
}

@import "../../assets/scss/colors";
@import "../../assets/scss/responsive";

body {
	font-family: 'Nunito Sans', sans-serif;
    font-weight: 600;
}

.invalid {
	input {
		border: 0.1rem solid red;
		background-color: #3f1d1da4;
		transition: 300ms;
		&:hover {
			border: 0.1rem solid red;
			background-color: #303245;
		}
	}
	p {
		color: rgb(218, 63, 63);
		margin-top: 0;
		transition: 400ms;
	}
}

$black: #041c25;
$white: #ffffff;
$red: #ea5123;
$light-red: #ebad99;
$gray: #efebe6;

/* 3D Cube */
$cubeWidth: 70px;
$cubeHeight: 70px;
$cubeDepth: $cubeHeight;

._3d-wrap {
	position: relative;
	transform: scale(0.8);
	.image--3d {
		opacity: 0;
	}
	.space3d {
		perspective: 1000px;
		width: $cubeWidth;
		height: 100%;
		position: absolute;
		top: 0;
		left: 50%;
		transform: translate(-80%, 0);
		text-align: center;
		display: inline-block;
	}
	._3dbox {
		display: inline-block;
		transition: all 0.85s cubic-bezier(0.175, 0.885, 0.32, 1.275);

		text-align: center;
		position: relative;
		width: 100%;
		height: 100%;
		transform-style: preserve-3d;
		transform: rotateX(-15deg) rotateY(15deg);
		._3dface {
			overflow: hidden;
			position: absolute;

			border: 1px solid #888;
			background: #fff;
			box-shadow: inset 0 0 60px rgba(0, 0, 0, 0.1), 0 0 50px rgba(0, 0, 0, 0.3);
			color: #333;
			line-height: $cubeWidth;
			opacity: 0.8;
			img {
				height: 100%;
				width: auto;
			}
			&--front {
				// width: $cubeWidth;
				// height: $cubeHeight;
				height: 100%;
				width: auto;
				transform: translate3d(0, 0, calc($cubeDepth / 2));
			}
			&--top {
				width: 150px;
				height: $cubeDepth;
				transform: rotateX(90deg) translate3d(0, 0, calc($cubeHeight / 2));
			}
			&--left {
				width: $cubeDepth;
				height: $cubeHeight;
				left: 50%;
				height: 100%;
				margin-left: calc($cubeDepth / -2);
				transform: rotateY(-90deg) translate3d(0, 0, calc($cubeWidth / 2));
			}
		}
	}
}

.site {
    width: 100vw;
    overflow: hidden;
	background-color: $white;
	box-sizing: border-box;
	color: $black;
	a {
		color: $black;
	}
	font-family: "Rubik", sans-serif, system-ui, -apple-system;
	&__title {
		color: $white;
		text-decoration: underline;
		text-decoration-color: $red;
		font-size: 39px;
		line-height: 49px;
	}

	&__description {
		padding-top: 2em;
		padding-bottom: 2em;
		.description__content {
			counter-reset: p-tag;
			p {
				counter-increment: p-tag;
				position: relative;
				margin-left: 2em;
				padding-bottom: 1em;
				border-bottom: 2px solid red;
				&:last-child {
					border-bottom: none;
				}
				&::before {
					content: "0" counter(p-tag) "\a0\a0";
					font-weight: 600;
					position: absolute;
					left: -2em;
					top: 0;
				}
			}
		}
	}

	&__faq {
		padding: 2em 0;
		&-item {
			border-top: 1px solid $white !important;
			padding-left: 0 !important;
			margin-left: 2em !important;
		}
	}
	$logo-height: 70px;
	&__logo {
		position: relative;
		display: block;
		&--image {
			display: flex;
			position: relative;
			justify-content: center;
			height: $logo-height;
			overflow: hidden;
			svg {
				height: $logo-height;
				width: auto;
			}
		}
	}
    #product {
        margin-bottom: 36px;
        .thumbnail {
            aspect-ratio: 1;
            height: auto;
            padding: 36px;
            display: flex;
            align-items: center;
            justify-content: center;
            overflow: hidden;
            position: relative;
            img {
                height: 100%;
                width: auto;
                left: 50%;
                position: absolute;
                translate: -50% 0;
            }
        }
        .product__detail {
            height: 100%;
            padding: 36px;
            color: #505050;
            a {
                color: #505050;
            }
            h3 {
                margin-top: 0;
            }
            h4 {
                margin: 0;
            }
            p {
                margin-top: 0;
            }
        }
    }
    #pricing-plans {
        .options {
            background-color: $light-blue;
            h3 {
                margin: 0;
                font-size: 24px;
                font-weight: 600;
                height: 88px;
                line-height: 88px;
                color: $white;
                text-align: center;
                overflow: hidden;
                border-bottom: 2px solid $blue;
                padding: 0 36px;
            }
            .list {
                padding: 0 36px;
                margin-top: 1em;
                li {
                    display: flex;
                    align-items: center;
                    height: 72px;
                    overflow: hidden;
                    font-size: 18px;
                    line-height: 1.5em;
                }
            }
        }
        .pricing_table {
            &__column {
                width: 100%;
                height: 100%;
    
                > .MuiCard-root {
                    display: "flex";
                    flex-direction: "column";
                    border-radius: "0.8rem";
                    box-shadow: "0rem 0.5rem 0.2rem 0.2rem #000";
                    padding: 1;
                    border-radius: 0;
                    height: 100%;
                    background-color: transparent;
                }
                .MuiCardContent-root {
                    padding: 0;
                }
                .column {
                    &__title {
                        position: relative;
                        font-weight: 700;
                        white-space: nowrap;
                        background-color: rgba(255,255,255, 0.6);
                        border-bottom: 2px solid $blue;
                        h3 {
                            font-size: 24px;
                            height: 88px;
                            line-height: 88px;
                            color: $blue;
                        }
                        .wrap {
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            align-items: center;
                        }
                    }
    
                    // &__title .wrap::before,
                    // &__title .wrap::after,
                    // &__title::after,
                    // &__title::before {
                    // 	content: " ";
                    // 	width: 24px;
                    // 	height: 24px;
                    // 	position: absolute;
                    // 	top: 0;
                    // 	left: 0;
                    // 	border-width: 4px 0 0 4px;
                    // 	border-color: $black;
                    // 	border-style: solid;
                    // 	display: block;
                    // }
                    // &__title::before {
                    // 	left: auto;
                    // 	right: 0;
                    // 	border-width: 4px 4px 0 0;
                    // }
                    // &__title .wrap::before {
                    // 	top: auto;
                    // 	bottom: 0;
                    // 	border-width: 0 0 4px 4px;
                    // }
                    // &__title .wrap::after {
                    // 	top: auto;
                    // 	left: auto;
                    // 	bottom: 0;
                    // 	right: 0;
                    // 	border-width: 0 4px 4px 0;
                    // }
    
                    &__button {
                        background-color: $blue;
                        border-radius: 0;
                        text-transform: uppercase;
                        color: $white;
                        font-weight: 600;
                        font-size: 18px;
                        letter-spacing: -0.6px;
                        margin-top: 10px;
                        &:hover {
                            background-color: darken($blue, 5%);
                        }
                        &:disabled {
                            background-color: #757575;
                        }
                    }
    
                    &__description {
                        padding-top: 1em;
                        background-color: $white;
                        display: flex;
                        align-items: center;
                        flex-direction: column;
                        .MuiSvgIcon-root {
                            margin-right: 0;
                        }
                    }

                    &__actions {
                        padding: 36px;
                        background-color: $white;
                    }
                }
                
                .cross,
                .tick {
                    overflow: hidden;
                    color: $white;
                    height: 72px;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;
                    > span {
                        background-color: $bright-blue; 
                        height: 36px;
                        width: 36px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        border-radius: 50%;
                    }
                    svg {
                        color: $white;
                        font-size: 20px;
                    }
                }
                
                .cross > span {
                    background-color: $blue;
                }
    
                form:not(.have-email) {
                    margin-top: 4.3em !important;
                }
            }
        }
    }

    .video {
        &__player {
            position: relative;
            video {
                object-fit: contain;
                aspect-ratio: 1;
            }
            .play-button {
                position: absolute;
                top: 50%;
                left: 50%;
                translate: -50% -50%;
                width: 90px;
                height: 90px;
                display: block;
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;
            }

            &:not(.playing) {
                .play-button {
                    background-color: $blue;
                    .arrow {
                        margin-left: 4%;
                        width: 0; 
                        height: 0; 
                        border-top: 12px solid transparent;
                        border-bottom: 12px solid transparent;
                        border-left: 24px solid $white;
                    }
                }
            }
            &.playing {
                .play-button {
                    background-color: rgba($color: $white, $alpha: 0.5);
                    display: none;
                    .arrow {
                        width: 50%;
                        height: 50%;
                        background-color: rgba($color: $white, $alpha: 0.3);
                    }
                }
                &:hover .play-button {
                    display: flex;
                }
            }
        }
    }

    .section {
        margin-bottom: 36px;
        > .row {
            margin-left: -16px;
            margin-right: -16px;
            > .col {
                padding: 70px;
            }
        }
        h3 {
            font-size: 36px;
            line-height: 1.5em;
            font-weight: 700;
        }
        &--bg-gray {
            background-color: #EBEBEB;
        }
        &--bg-blue {
            background-color: $blue;
        }
        @media screen and (max-width: 900px ){
            margin-bottom: 0;
        }
    }
   
    #benefits {
        .list {
            .item {
                padding: 12px 0;
                border-bottom: 1px solid $white;
                &:last-child {
                    border-bottom: none;
                }
                span {
                    display: block;
                    font-weight: 700;
                    font-size: 18px;
                    line-height: 1.5em;
                }
            }
        }
    }
}

/* Absolute Center Spinner */
.loading {
	position: fixed;
	z-index: 999;
	height: 2em;
	width: 2em;
	overflow: show;
	margin: auto;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
}

/* Transparent Overlay */
.loading:before {
	content: "";
	display: block;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: radial-gradient(rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.8));

	background: -webkit-radial-gradient(rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.8));
}

/* :not(:required) hides these rules from IE9 and below */
.loading:not(:required) {
	/* hide "loading..." text */
	font: 0/0 a;
	color: transparent;
	text-shadow: none;
	background-color: transparent;
	border: 0;
}

.loading:not(:required):after {
	content: "";
	display: block;
	font-size: 10px;
	width: 1em;
	height: 1em;
	margin-top: -0.5em;
	-webkit-animation: spinner 1250ms infinite linear;
	-moz-animation: spinner 1250ms infinite linear;
	-ms-animation: spinner 1250ms infinite linear;
	-o-animation: spinner 1250ms infinite linear;
	animation: spinner 1250ms infinite linear;
	border-radius: 0.5em;
	-webkit-box-shadow: rgba(104, 104, 104, 0.75) 1.5em 0 0 0, rgba(104, 104, 104, 0.75) 1.1em 1.1em 0 0,
		rgba(104, 104, 104, 0.75) 0 1.5em 0 0, rgba(104, 104, 104, 0.75) -1.1em 1.1em 0 0,
		rgba(104, 104, 104, 0.75) -1.5em 0 0 0, rgba(104, 104, 104, 0.75) -1.1em -1.1em 0 0,
		rgba(104, 104, 104, 0.75) 0 -1.5em 0 0, rgba(104, 104, 104, 0.75) 1.1em -1.1em 0 0;
	box-shadow: rgba(104, 104, 104, 0.75) 1.5em 0 0 0, rgba(104, 104, 104, 0.75) 1.1em 1.1em 0 0,
		rgba(104, 104, 104, 0.75) 0 1.5em 0 0, rgba(104, 104, 104, 0.75) -1.1em 1.1em 0 0,
		rgba(104, 104, 104, 0.75) -1.5em 0 0 0, rgba(104, 104, 104, 0.75) -1.1em -1.1em 0 0,
		rgba(104, 104, 104, 0.75) 0 -1.5em 0 0, rgba(104, 104, 104, 0.75) 1.1em -1.1em 0 0;
}

/* Animation */

@-webkit-keyframes spinner {
	0% {
		-webkit-transform: rotate(0deg);
		-moz-transform: rotate(0deg);
		-ms-transform: rotate(0deg);
		-o-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		-moz-transform: rotate(360deg);
		-ms-transform: rotate(360deg);
		-o-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}
@-moz-keyframes spinner {
	0% {
		-webkit-transform: rotate(0deg);
		-moz-transform: rotate(0deg);
		-ms-transform: rotate(0deg);
		-o-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		-moz-transform: rotate(360deg);
		-ms-transform: rotate(360deg);
		-o-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}
@-o-keyframes spinner {
	0% {
		-webkit-transform: rotate(0deg);
		-moz-transform: rotate(0deg);
		-ms-transform: rotate(0deg);
		-o-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		-moz-transform: rotate(360deg);
		-ms-transform: rotate(360deg);
		-o-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}
@keyframes spinner {
	0% {
		-webkit-transform: rotate(0deg);
		-moz-transform: rotate(0deg);
		-ms-transform: rotate(0deg);
		-o-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		-moz-transform: rotate(360deg);
		-ms-transform: rotate(360deg);
		-o-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}
